.user-item {
  margin: 1rem auto;
  width: 80%;
  min-width: 17.5rem;
}

.user-item a {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  padding: 1rem;
  color: white;
  background: #292929;
}

.user-item a:hover,
.user-item a:active {
  background: #ffd900;
}

.user-item-content {
  padding: 0;
}

.user-item-image {
  width: 4rem;
  height: 4rem;
  margin-right: 1rem;
}

.user-item-info h2 {
  font-size: 1.5rem;
  margin: 0 0 0.5rem 0;
  font-weight: normal;
  color: #ffd900;
}

.user-item:hover h2,
.user-item:active h2,
.user-item:hover h3,
.user-item:active h3 {
  color: #292929;
}
.user-item-info {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;

}
.user-item-info h3 {
  margin: 0;
}