.place-item {
  margin: 1rem 0;
}

.place-item-content {
  padding: 0;
}

.admin-post {
  width: 80%;
  margin: 0 auto;
}

.place-item-info {
  padding: 1rem;
  text-align: left;
}

.place-item-image {
  width: 80%;
  margin: 0 auto;
  height: 12.5rem;
}

.place-item-image img {
width: 100%;
height: 100%;
object-fit: cover;
}


.place-item-info h2,
.place-item-info h3,
.place-item-info p {
margin: 0 0 0.5rem 0;
}

.place-item-actions {
padding: 1rem;
text-align: center;
border-top: 1px solid #ccc;
}

.place-item-modal-content {
padding: 0;
}

.place-item-modal-actions {
text-align: right;
}

.place-item-actions button,
.place-item-actions a {
margin: 0.5rem;
}

.map-container {
height: 15rem;
width: 100%;
}

.author-data {
  margin-bottom: 12px;
}

.author-data a{
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

@media (min-width: 768px) {
.place-item-image {
height: 16rem;
}
}